import React,{useState, useReducer, useEffect, useContext} from 'react'
import axios from 'axios'

import { getResponce } from '../../App'
import data from '../../data.json'

function Demographic() {
    const captureQue1Values = useContext(getResponce)
    const [user, setUser] = useState()
    let userLength 
    const reducer = (state, action)=>{
        switch(action.type){
            case 'demo':
                return {...state, [action.name]:action.value}
            case 'default' :
                return state    
        }
    }

    const [userNew, dispatch] = useReducer(reducer, user)

    
        
  

    const radioClicked = (n, d) => {
        dispatch({type:'demo', value:d, name: n})
        captureQue1Values.countDispatch({type:'que1', value:userNew})
    }
    
    useEffect(()=>{
        if(userNew){
            if(Object.keys(userNew).length === data.que1.q.length){
                captureQue1Values.countDispatch({type:'que1', value:userNew})
                captureQue1Values.countDispatch({type:'count', value:2})
            }
            console.log(Object.keys(userNew).length )

        }
    },[userNew])

    // const nextPrev = () =>{
    //     if( Object.keys( ).length === 14){
    //         captureQue1Values.countDispatch({type:'que1', value:userNew})
    //     } else{
    //        // alert("Please complete all the questions")
    //     }
        
    // }
    // useEffect(()=>{})
    // nextPrev()
    //console.log("userNew" +  Object.keys(userNew).length)
    // if( Object.keys(userNew).length === 14){
    //     captureQue1Values.countDispatch({type:'que1', value:userNew})
    // }
    return (
        <div className="section" id="demographic" >
            
                <div className="panel panel-default">
                    <div className="panel-heading">{data.que1.sec}</div>
                    <div className="panel-body">
                        <form  id="demo">
                            
                            {data.que1.q.map( res =>
                                <div className="form-group ">
                                    <label >{res.label}</label>
                                        <div className="radioInline">
                                            {res.des.map(desc => 
                                            <label for={desc} >
                                                <input type="radio" id={res.label} name={res.name} value={desc} onClick={ ()=> radioClicked(`${res.name}`,`${desc}`)}/>{desc}
                                            </label>)}
                                            {res.name === "religion" ? <label for={res.name} >Others please specify <input type="text" id={res.label} name={res.name}  onChange={ e => radioClicked(`${res.name}`,`${e.target.value}`)}/></label> :
                                            res.name === "education" ? <label for={res.name} >Professional Course (CA/ CS/ CMA etc.) please specify <input type="text" id={res.label} name={res.name}  onChange={ e => radioClicked(`${res.name}`,`${e.target.value}`)}/></label> :
                                            res.name === "industry" ? <label for={res.name} >Other industry please specify <input type="text" id={res.label} name={res.name}  onChange={ e => radioClicked(`${res.name}`,`${e.target.value}`)}/></label> :
                                            res.name === "occupation" ? <label for={res.name} >If Service, please specify <select type="text" id={res.label} name={res.name}  onChange={ e => radioClicked(`${e.target.name}`,`${e.target.value}`)} >
                                                <option name={res.name} value="State Government" >State Government</option>
                                                <option name={res.name} value="Central Government" >Central Government</option>
                                                <option name={res.name} value="Government Local bodies" >Government Local bodies</option>
                                                <option name={res.name} value="Private Sector" >Private Sector</option>
                                                </select></label> :
                                            res.name === "residence" ? 
                                                <><label for={res.name} >Metro City <input type="text" id={res.label} name={res.name}  onChange={ e => radioClicked(`${res.name}`,`${e.target.value}`)} placeholder="Mumbai/ Delhi/ Bangalore/ Kolkata/ Chennai"/></label>
                                                <label for={res.name} >II tier City <input type="text" id={res.label} name={res.name}  onChange={ e => radioClicked(`${res.name}`,`${e.target.value}`)} placeholder="Pune /Cochin/ Jaipur/Lucknow etc."/></label>
                                                <label for={res.name} >Metro City <input type="text" id={res.label} name={res.name}  onChange={ e => radioClicked(`${res.name}`,`${e.target.value}`)} placeholder="Coimbatore / Nashik etc."/></label></> :    
                                            '' }
                                        </div>   
                                </div>
                            )}
                            
                            
                            
                        </form>
                        {/* <button className="btn btn-primary next" onClick={()=>nextPrev()}>Next</button> */}
                    </div>
                </div>
            
            
        </div>
    )
}

export default Demographic
